// app/event/[eventId]/EventClient.tsx
"use client";

import { useEffect, useState } from "react";
import { requiredStakeXrp } from "@/lib/stake";

export default function EventClient({ eventId }: { eventId: string }) {
  const [wallet, setWallet] = useState("");
  const [score, setScore] = useState<number | null>(null);
  const [loadingScore, setLoadingScore] = useState(false);

  const stake = score === null ? null : requiredStakeXrp(score);

  useEffect(() => {
    let cancelled = false;

    async function load() {
      if (!wallet.trim()) {
        setScore(null);
        return;
      }
      setLoadingScore(true);
      try {
        const res = await fetch(`/api/score?wallet=${encodeURIComponent(wallet.trim())}`);
        const data = await res.json();
        if (!cancelled) setScore(data.score ?? 0);
      } finally {
        if (!cancelled) setLoadingScore(false);
      }
    }

    load();
    return () => {
      cancelled = true;
    };
  }, [wallet]);

  return (
    <>
      <h2>Join Queue (MVP preview)</h2>
      <div style={{ opacity: 0.7, marginBottom: 10 }}>Event: {eventId}</div>

      <div style={{ display: "flex", gap: 12, alignItems: "center", flexWrap: "wrap" }}>
        <label>
          Wallet address (for demo):
          <input
            value={wallet}
            onChange={(e) => setWallet(e.target.value)}
            placeholder="r... (paste)"
            style={{ marginLeft: 8, width: 420, padding: 8 }}
          />
        </label>
      </div>

      <div style={{ marginTop: 12 }}>
        {loadingScore && <div>Loading scoreâ€¦</div>}
        {!loadingScore && score !== null && (
          <div>
            <div><b>FenScore:</b> {score}</div>
            <div><b>Required stake:</b> {stake} XRP</div>
          </div>
        )}
        {!loadingScore && score === null && (
          <div style={{ opacity: 0.7 }}>
            Enter a wallet to see score and stake requirement.
          </div>
        )}
      </div>
    </>
  );
}
